.card:before {
  content: "";
  display: block;
  height: 4px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
}

.card {
  /* border: 1px solid red; */
  background-color: var(--cardGray);
  padding: 20px;
  text-align: center;
  border-radius: 6px;
  position: relative;
  overflow: hidden;
}

.card.facebook:before {
  background-color: var(--facebook);
}

.card.twitter:before {
  background-color: var(--twitter);
}

.card.instagram:before {
  background: linear-gradient(
    to right,
    var(--instagram-1) 0%,
    var(--instagram-2) 100%
  );
}

.card.youtube:before {
  background-color: var(--youTube);
}

.card-header {
  display: flex;
  align-content: center;
  justify-content: center;
  font-size: 0.9em;
  font-weight: bold;
  color: var(--gray);
  padding: 10px 0 20px 0;
}

.card-header img {
  padding-right: 4px;
}

.card-number {
  font-weight: 700;
  font-size: 50px;
}

.card-title {
  text-transform: uppercase;
  font-size: 0.8em;
  letter-spacing: 4px;
  color: var(--gray);
}

.card-today {
  font-weight: bold;
  font-size: 0.7em;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  color: var(--lime-green);
}

.card-today img {
  padding-right: 4px;
}
